/* ==========================================================================
   Utility classes for transitions.

   Adds transitions utilty classes for transform, opacity,
   and for the removing the transition duration.
   ========================================================================== */
.u-no-animation {
  transition-duration: 0s !important;
}

.u-move-transition {
  transition: transform 0.25s ease-out;
}

.u-move-to-origin {
  transform: translate3d(0, 0, 0);
}

.u-move-left {
  transform: translate3d(-100%, 0, 0);
}

.u-move-left-2x {
  transform: translate3d(-200%, 0, 0);
}

.u-move-left-3x {
  transform: translate3d(-300%, 0, 0);
}

.u-move-right {
  transform: translate3d(100%, 0, 0);
}

.u-move-up {
  transform: translate3d(0, -100%, 0);
}

.u-alpha-transition {
  transition: opacity 0.25s linear;
}

.u-alpha-100 {
  opacity: 1;
}

.u-alpha-0 {
  opacity: 0;
}

.u-max-height-transition {
  overflow: hidden;
  contain: paint;
  transition: max-height 0.2s ease-out;
}

/*
.u-max-height-default {
  This class is kept for documentation completeness.
  The actual max-height is set in the JavaScript,
  so that we know what the actual height of the content is for easing purposes.
}
*/
.u-max-height-zero {
  max-height: 0 !important;
}

.u-max-height-summary {
  /* The value set here should show 4 lines of text at our standard 16px
     base font size. The calculation comes from the following:
     88px = 16 * 5.5em.
     5.5em = base-line-height (22px) * 4 / base-font-size (16px)
  */
  max-height: 88px !important;
}

.o-banner {
  padding: 0.9375em 0;
  background: var(--gold-10);
  border-bottom: 1px solid var(--gray-40);
  font-size: 0.875em;
}
.o-banner .m-notification {
  border: none;
  padding: 0;
}
.o-banner .m-notification__icon {
  left: 0;
  top: 0;
}
.o-banner--dark {
  background: var(--teal-dark);
  border-color: var(--teal-dark);
  color: var(--white);
}
.o-banner--dark .m-notification {
  background: var(--teal-dark);
}
.o-banner--dark .m-notification .cf-icon-svg {
  fill: var(--white);
}
.o-banner--dark .m-notification a {
  color: var(--white);
  border-color: var(--white);
}
.o-banner--dark .m-notification a:visited, .o-banner--dark .m-notification a.visited {
  border-color: var(--teal-40);
  color: var(--teal-40);
}
.o-banner--dark .m-notification a:hover, .o-banner--dark .m-notification a.hover {
  border-color: var(--gray-15);
  color: var(--gray-15);
}
.o-banner--dark .m-notification a:focus, .o-banner--dark .m-notification a.focus {
  border-color: var(--white);
  color: var(--white);
}
.o-banner--dark .m-notification a:active, .o-banner--dark .m-notification a.active {
  border-color: var(--gray-15);
  color: var(--gray-15);
}
@media only all and (min-width: 37.5625em) {
  .o-banner {
    font-size: 1em;
  }
}